import '../scss/PermissionPage.scss';
import React, {useState} from 'react';
import {
    Navbar,
    Container,
    Button,
    Table,
    Dropdown,
    DropdownButton,
} from 'react-bootstrap';
import axios from 'axios';
import {useHistory} from 'react-router-dom';
import author from '../img/author.png';

function PermissionPage(props) {
    let history = useHistory();
    let [permissionState, setPermissionstate] = useState(false);
    const [workerArray, setworkerArray] = useState([]);
    const [memberArray, setmemberArray] = useState([]);

    const onLogoutHandler = (event) => {
        event.preventDefault();
        axios.post('/api/logout').then((res) => {
            console.log(res);
            if (res.data == '') {
                props.setisAuthorized(false);
                alert('  !');
                history.push('/');
            } else {
                alert('  !');
            }
        });
    };

    const onWorkerHandler = (event) => {
        event.preventDefault();
        axios.get('/api/workers').then((res) => {
            setworkerArray(res.data);
        });
        setPermissionstate(false);
    };

    const onMemberHandler = (event) => {
        event.preventDefault();
        axios.get('/api/members').then((res) => {
            setmemberArray(res.data);
        });
        setPermissionstate(true);
    };

    return (
        <div className="permissionPage">
            <div className="topNavbar">
                <Navbar bg="primary" variant="dark" style={{padding: '30px'}}>
                    <Container>
                        <Navbar.Brand className="leftbox">
                             
                        </Navbar.Brand>
                        <Navbar.Toggle />
                        <Navbar.Collapse className="justify-content-end">
                            <div className="rightbox">
                                <Navbar.Text>
                                    .
                                    <span className="name"></span>
                                </Navbar.Text>
                                <Button
                                    variant="secondary"
                                    type="submit"
                                    onClick={onLogoutHandler}>
                                    
                                </Button>
                            </div>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </div>
            <div className="maincontent">
                <br></br>
                <br></br>
                <div class="container white-box">
                    <div class="row">
                        <div class="col-7">
                            <div class="d-flex" style={{minwidth: '574px'}}>
                                <div class="flex-shrink-0">
                                    <img src={author} />
                                </div>
                                <div class="flex-grow-1 profile">
                                    <h3> .</h3>
                                    <br></br>
                                    <p>
                                            .
                                        <br></br>
                                          ,   , 
                                        <br></br>
                                         {' '}
                                        <strong style={{color: 'blue'}}>
                                             
                                        </strong>
                                         {' '}
                                        <strong style={{color: 'blue'}}>
                                            
                                        </strong>
                                        .
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="row">
                                <div className="proBtn">
                                    <div class="col-12">
                                        <Button
                                            variant="primary"
                                            type="submit"
                                            style={{
                                                marginTop: '8px',
                                                paddingLeft: '40px',
                                                paddingRight: '40px',
                                            }}
                                            onClick={onWorkerHandler}>
                                             
                                        </Button>
                                    </div>

                                    <Button
                                        variant="primary"
                                        type="submit"
                                        style={{
                                            marginTop: '8px',
                                            paddingLeft: '40px',
                                            paddingRight: '40px',
                                        }}
                                        onClick={onMemberHandler}>
                                         
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {permissionState === false ? (
                <WorkerState workerArray={workerArray}></WorkerState>
            ) : (
                <UserState memberArray={memberArray}></UserState>
            )}
        </div>
    );
}

function WorkerState(props) {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [permission, setPermission] = useState('');

    const onPermissionHandler = (event) => {
        event.preventDefault();
        axios
            .post('/api/member', {
                name: name,
                email: email,
                permission: permission,
            })
            .then(() => {
                alert(
                    '  .\n' +
                        ' " "   .'
                );
            })
            .catch(() => {
                alert('.');
            });
    };

    return (
        <div className="subcontent">
            <div className="container white-box add">
                <div className="worker">
                    <h2> </h2>
                    <div className="workerInfo" style={{overflow: 'auto'}}>
                        <div>
                            <Table responsive="lg">
                                <thead style={{textAlign: 'center'}}>
                                    <tr>
                                        <th></th>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                    </tr>
                                </thead>
                                <tbody
                                    style={{
                                        textAlign: 'center',
                                        verticalAlign: 'middle',
                                    }}>
                                    {props.workerArray.map((a, i) => {
                                        return (
                                            <tr>
                                                <td>{i + 1}</td>
                                                <td>{a.name}</td>
                                                <td>{a.email}</td>
                                                <td>{a.permission}</td>
                                                <td>
                                                    <DropdownButton
                                                        id="dropdown-basic-button"
                                                        title=" ">
                                                        <Dropdown.Item
                                                            onClick={
                                                                onPermissionHandler
                                                            }>
                                                             
                                                        </Dropdown.Item>
                                                        <Dropdown.Item
                                                            onClick={
                                                                onPermissionHandler
                                                            }>
                                                            
                                                        </Dropdown.Item>
                                                        <Dropdown.Item
                                                            onClick={
                                                                onPermissionHandler
                                                            }>
                                                            
                                                        </Dropdown.Item>
                                                    </DropdownButton>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

function UserState(props) {
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');

    const onNameHandler = (event) => {
        setName(event.currentTarget.value);
    };

    const onPhoneHandler = (event) => {
        setPhone(event.currentTarget.value);
    };

    const onAddmemberHandler = (event) => {
        event.preventDefault();
        axios
            .post('/api/member', {
                name: name,
                phone: phone,
            })
            .then(() => {
                alert(
                    '   .\n' +
                        ' " "   .'
                );
            })
            .catch(() => {
                alert('  .');
            });
    };

    return (
        <div className="subcontent">
            <div className="container white-box add">
                <div className="member">
                    <div className="d-flex">
                        <div className="col-4">
                            <h2>  ( )</h2>
                        </div>
                        <div className="col-4"></div>
                        <div className="col-4">
                            <input
                                className="inputName"
                                type="text"
                                placeholder=" "
                                value={name}
                                onChange={onNameHandler}
                                style={{
                                    textAlign: 'center',
                                    padding: '8px',
                                }}
                            />
                            <input
                                className="inputPhone"
                                type="text"
                                placeholder="  "
                                value={phone}
                                onChange={onPhoneHandler}
                                style={{
                                    textAlign: 'center',
                                    padding: '8px',
                                }}
                            />
                            <Button
                                variant="secondary"
                                type="submit"
                                style={{
                                    padding: '7px',
                                    marginBottom: '4px',
                                }}
                                onClick={onAddmemberHandler}>
                                 
                            </Button>
                        </div>
                    </div>

                    <div className="memberInfo" style={{overflow: 'auto'}}>
                        <div>
                            <Table responsive="lg">
                                <thead style={{textAlign: 'center'}}>
                                    <tr>
                                        <th></th>
                                        <th> </th>
                                        <th> </th>
                                        <th> </th>
                                    </tr>
                                </thead>
                                <tbody
                                    style={{
                                        textAlign: 'center',
                                        verticalAlign: 'middle',
                                    }}>
                                    {props.memberArray.map((a, i) => {
                                        return (
                                            <tr>
                                                <td>{i + 1}</td>
                                                <td>{a.name}</td>
                                                <td>
                                                    {a.phone.slice(0, [3]) +
                                                        ' - ' +
                                                        a.phone.slice(3, [7]) +
                                                        ' - ' +
                                                        a.phone.slice(7, [11])}
                                                </td>
                                                <td>
                                                    <Button
                                                        variant="outline-primary"
                                                        type="submit"
                                                        style={{
                                                            paddingLeft: '20px',
                                                            paddingRight:
                                                                '20px',
                                                        }}
                                                        value={a.phone}
                                                        onClick={(event) => {
                                                            event.preventDefault();
                                                            axios
                                                                .post(
                                                                    '/api/member/delete/',
                                                                    {
                                                                        phone: a.phone,
                                                                    }
                                                                )
                                                                .then(() => {
                                                                    alert(
                                                                        '  .'
                                                                    );
                                                                })
                                                                .catch(() => {
                                                                    alert(
                                                                        '  . " "   .'
                                                                    );
                                                                });
                                                        }}>
                                                         
                                                    </Button>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </Table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default PermissionPage;
