import '../scss/SearchPage.scss';
import React, {useState} from 'react';
import {Navbar, Container, Button, ListGroup} from 'react-bootstrap';
import axios from 'axios';
import {useHistory} from 'react-router-dom';
import author from '../img/author.png';

function SearchPage(props) {
    let history = useHistory();
    const [phone, setPhone] = useState('');
    let [searchState, setSearchstate] = useState('0');

    const onPhoneHandler = (event) => {
        setPhone(event.currentTarget.value);
    };

    const onSearchHandler = (event) => {
        event.preventDefault();
        axios
            .get('/api/member/', {
                params: {phone: phone},
            })
            .then((res) => {
                if (res.data === true) {
                    setSearchstate('1');
                    alert(' !  .');
                } else if (res.data === false) {
                    setSearchstate('2');
                    alert(' !  .');
                } else {
                    setSearchstate('3');
                    alert('  .  .');
                }
            })
            .catch((error) => {
                alert(' ');
                setSearchstate(false);
                console.log(error);
            });
    };

    const onLogoutHandler = (event) => {
        event.preventDefault();
        axios.post('/api/logout').then((res) => {
            console.log(res);
            if (res.data == '') {
                props.setisAuthorized(false);
                alert('  !');
                history.push('/');
            } else {
                alert('  !');
            }
        });
    };

    const onPermissionHandler = (event) => {
        event.preventDefault();
        axios.get('/api/members').then((res) => {
            if (res.data === '') {
                alert('  .  .');
            } else {
                props.setisPermission(true);
                history.push('/permission');
            }
        });
    };

    return (
        <div className="searchPage">
            <div className="topNavbar">
                <Navbar bg="primary" variant="dark" style={{padding: '30px'}}>
                    <Container>
                        <Navbar.Brand className="leftbox">
                              
                        </Navbar.Brand>
                        <Navbar.Toggle />
                        <Navbar.Collapse className="justify-content-end">
                            <div className="rightbox">
                                <Navbar.Text>
                                    .
                                    <span className="name">
                                        {' '}
                                        {props.showID}
                                    </span>
                                    
                                </Navbar.Text>
                                <Button
                                    variant="secondary"
                                    type="submit"
                                    onClick={onLogoutHandler}>
                                    
                                </Button>
                            </div>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </div>
            <div className="maincontent">
                <br></br>
                <br></br>
                <div class="container white-box">
                    <div class="row">
                        <div class="col-7">
                            <div class="d-flex" style={{minwidth: '574px'}}>
                                <div class="flex-shrink-0">
                                    <img src={author} />
                                </div>
                                <div class="flex-grow-1 profile">
                                    <h3>  .</h3>
                                    <p>
                                            .
                                        <br></br>
                                          .
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="col-4">
                            <div class="row">
                                <div className="proBtn">
                                    <div class="col-12">
                                        <input
                                            type="text"
                                            placeholder="  ."
                                            value={phone}
                                            onChange={onPhoneHandler}
                                            style={{
                                                textAlign: 'center',
                                                padding: '8px',
                                            }}
                                        />
                                    </div>
                                    <Button
                                        variant="primary"
                                        type="submit"
                                        style={{
                                            marginTop: '8px',
                                        }}
                                        onClick={onSearchHandler}>
                                        
                                    </Button>
                                    <Button
                                        variant="primary"
                                        type="submit"
                                        style={{
                                            marginTop: '8px',
                                            marginLeft: '12px',
                                        }}
                                        onClick={onPermissionHandler}>
                                         
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {searchState === '1' ? (
                <ResultTrue></ResultTrue>
            ) : searchState === '2' ? (
                <ResultFalse></ResultFalse>
            ) : searchState === '3' ? (
                <ResultError></ResultError>
            ) : (
                <ResultStart></ResultStart>
            )}
        </div>
    );
}

function ResultStart() {
    return (
        <div className="subcontent">
            <div className="container white-box">
                <div className="subtext">
                    <h2>     </h2>
                    <p>
                        <br></br>
                           "-"   .
                        <br></br> <br></br>
                        . 01012345678     
                    </p>
                </div>
            </div>
        </div>
    );
}

function ResultTrue() {
    return (
        <div className="subcontent">
            <div className="container white-box">
                <div className="subtext">
                    <h2>   . </h2>
                    <p>
                        <br></br>
                            .
                        <br></br>
                        ,     
                        <br></br>
                            .
                    </p>
                </div>
            </div>
        </div>
    );
}

function ResultFalse() {
    return (
        <div className="subcontent">
            <div className="container white-box">
                <div className="subtext">
                    <h2>   . </h2>
                    <p>
                        <br></br>
                              .
                        <br></br>
                            .
                        <br></br>
                             
                        .
                    </p>
                </div>
            </div>
        </div>
    );
}

function ResultError() {
    return (
        <div className="subcontent">
            <div className="container white-box">
                <div className="subtext">
                    <h2>   . </h2>
                    <p>
                        <br></br>
                           .
                        <br></br>
                           .
                    </p>
                </div>
            </div>
        </div>
    );
}

export default SearchPage;
